.login {
    display: grid;
    place-items: center;
    height:100vh;
    background-color: #000;
}

.login > a {
    padding: 20px;
    background-color: #1db954;
    border-radius: 99px;
    font-weight: 650;
    color: #fff;
    text-decoration: none;
}

@media only screen and (max-width: 830px) {
    .login > img {
        width: 100%;
    }
  }